import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core';
import {AppBar, IconButton, Toolbar, Collapse} from '@material-ui/core';
import SortIcon from '@material-ui/icons/Sort';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Link as Scroll} from 'react-scroll';



const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
        fontFamily: 'Nunito',
    },
    appbar: {
        background: 'none',
    },
    appbarTitle: {
         flexGrow: '1'
    },
    appbarWraper: { 
        width: '80%',
        margin: '0 auto'
    },
    colorText: {
        color: '#5AFF3D'
    },
    title: {
        color: '#fff',
        fontSize: '4.5rem'
    },
    container: {
        textAlign: 'center'
    },
    goDown: {
        color: '#5AFF3D',
        fontSize: '4rem'
    }
    
}));

export default function Header () {
    const classes = useStyles();
    const [checked, setChecked] = useState(false);

    useEffect(() => {
        setChecked(true)
    }, [])

    return (
        <div className={classes.root} id='header'>
            <AppBar className={classes.appbar} elevation={0}>
            <Toolbar clasName={classes.appbarWrapper}>
                <h1 className={classes.appbarTitle}>
                    My<span className={classes.colorText}>Mountan.</span>
                </h1>
                <IconButton>
                    <SortIcon/>
                </IconButton>
            </Toolbar>
            </AppBar>
            <Collapse in={checked} {...(checked ? { timeout:3000 }:{})}>
             <div className={classes.container}>
                <h1 className={classes.title}>
                    Welcome to <br/> My<span className={classes.colorText}>Mountan.</span> 
                </h1>
                <Scroll to= 'place-to-visit' smooth ={true}>
                    <IconButton className={classes.goDown}>
                        <ExpandMoreIcon className={classes.goDown}/>
                    </IconButton>
                </Scroll>
                
                
               
            </div>
            </Collapse>
           
        </div>
       
    );
}
 
